import React, { useState, useEffect } from 'react';
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
  DragEndEvent,
} from '@dnd-kit/core';
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  verticalListSortingStrategy,
} from '@dnd-kit/sortable';
import {
  useSortable,
} from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import FileUpload from '../../components/FileUpload';
import DataExporter from '../../components/DataExporter';

interface Document {
  id: number;
  title: string;
  url: string;
  type: 'document' | 'certificate';
  sort_order: number;
  is_active: boolean;
  created_at: string;
  updated_at: string;
}

interface SortableDocumentProps {
  document: Document;
  index: number;
  onEdit: (document: Document) => void;
  onDelete: (id: number) => void;
}

const SortableDocument: React.FC<SortableDocumentProps> = ({ document, index, onEdit, onDelete }) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: document.id });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.5 : 1,
  };

  const getFileIcon = (type: string) => {
    if (type === 'certificate') {
      return (
        <svg width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
          <path d="M12 2L2 7l10 5 10-5-10-5zM2 17l10 5 10-5M2 12l10 5 10-5"/>
        </svg>
      );
    }
    return (
      <svg width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
        <path d="M14,2H6A2,2 0 0,0 4,4V20A2,2 0 0,0 6,22H18A2,2 0 0,0 20,20V8L14,2M18,20H6V4H13V9H18V20Z"/>
      </svg>
    );
  };

  return (
    <div
      ref={setNodeRef}
      style={style}
      className="admin-card"
    >
      <div className="flex justify-between items-start mb-4">
        <div className="flex items-start gap-4">
          <div
            {...attributes}
            {...listeners}
            className="cursor-grab active:cursor-grabbing p-2 hover:bg-gray-700 rounded"
            style={{ color: '#8B8B8B' }}
          >
            <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
              <path d="M11 18c0 1.1-.9 2-2 2s-2-.9-2-2 .9-2 2-2 2 .9 2 2zm-2-8c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2zm0-6c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2zm6 4c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2zm0 2c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2zm0 6c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2z"/>
            </svg>
          </div>
          <div className="flex-1">
            <div className="flex items-center gap-4 mb-2">
              <div 
                className="w-12 h-12 rounded-lg flex items-center justify-center"
                style={{ 
                  backgroundColor: document.type === 'certificate' ? '#D71920' : '#4A90E2',
                  color: '#F2F0F0'
                }}
              >
                {getFileIcon(document.type)}
              </div>
              <div>
                <h3 
                  className="text-xl font-semibold mb-1"
                  style={{
                    fontFamily: 'Roboto Flex',
                    fontWeight: 500,
                    color: '#F2F0F0'
                  }}
                >
                  {document.title}
                </h3>
                <div 
                  className="text-sm mb-2"
                  style={{
                    fontFamily: 'Inter',
                    color: '#B8B8B8'
                  }}
                >
                  Тип: {document.type === 'certificate' ? 'Сертификат' : 'Документ'} | 
                  URL: {document.url}
                </div>
                <div 
                  className="text-xs"
                  style={{ color: '#8B8B8B' }}
                >
                  Порядок: {document.sort_order} | 
                  Статус: {document.is_active ? 'Активен' : 'Неактивен'}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="flex gap-2">
          <button
            onClick={() => onEdit(document)}
            className="admin-button-secondary"
          >
            Редактировать
          </button>
          <button
            onClick={() => onDelete(document.id)}
            className="admin-button-danger"
          >
            Удалить
          </button>
        </div>
      </div>
    </div>
  );
};

const DownloadsAdmin: React.FC = () => {
  const [documents, setDocuments] = useState<Document[]>([]);
  const [loading, setLoading] = useState(true);
  const [editingDocument, setEditingDocument] = useState<Document | null>(null);
  const [isCreating, setIsCreating] = useState(false);
  const [uploadingFile, setUploadingFile] = useState(false);

  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  useEffect(() => {
    fetchDocuments();
  }, []);

  const fetchDocuments = async () => {
    try {
      const response = await fetch('/api/documents');
      if (response.ok) {
        const data = await response.json();
        setDocuments(data.data || []);
      }
    } catch (error) {
      console.error('Error fetching documents:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleEdit = (document: Document) => {
    setEditingDocument({ ...document });
    setIsCreating(false);
  };


  const handleSave = async () => {
    if (!editingDocument) return;

    try {
      const url = isCreating 
        ? '/api/documents'
        : `/api/documents/${editingDocument.id}`;
      
      const method = isCreating ? 'POST' : 'PUT';
      
      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(editingDocument),
      });

      if (response.ok) {
        const result = await response.json();
        if (isCreating) {
          setDocuments([...documents, result.data]);
        } else {
          setDocuments(documents.map(document => 
            document.id === editingDocument.id ? result.data : document
          ));
        }
        setEditingDocument(null);
        setIsCreating(false);
      } else {
        console.error('Error saving document');
      }
    } catch (error) {
      console.error('Error saving document:', error);
    }
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Вы уверены, что хотите удалить этот документ?')) return;

    try {
      const response = await fetch(`/api/documents/${id}`, {
        method: 'DELETE',
      });

      if (response.ok) {
        setDocuments(documents.filter(document => document.id !== id));
      } else {
        console.error('Error deleting document');
      }
    } catch (error) {
      console.error('Error deleting document:', error);
    }
  };

  const handleDragEnd = async (event: DragEndEvent) => {
    const { active, over } = event;

    if (over && active.id !== over.id) {
      const oldIndex = documents.findIndex(document => document.id === active.id);
      const newIndex = documents.findIndex(document => document.id === over.id);

      const newDocuments = arrayMove(documents, oldIndex, newIndex);
      setDocuments(newDocuments);

      // Обновляем порядок в базе данных
      try {
        const updates = newDocuments.map((document, index) => ({
          id: document.id,
          sort_order: index + 1
        }));

        const updatePromises = updates.map(update => 
          fetch(`/api/documents/${update.id}`, {
            method: 'PUT',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              ...documents.find(document => document.id === update.id),
              sort_order: update.sort_order
            }),
          })
        );

        await Promise.all(updatePromises);
      } catch (error) {
        console.error('Error updating sort order:', error);
        setDocuments(documents);
      }
    }
  };

  const handleCancel = () => {
    setEditingDocument(null);
    setIsCreating(false);
  };

  const uploadFile = async (file: File): Promise<string> => {
    const formData = new FormData();
    formData.append('file', file);
    
    const response = await fetch('/api/upload', {
      method: 'POST',
      body: formData,
    });
    
    if (!response.ok) {
      throw new Error('Ошибка загрузки файла');
    }
    
    const result = await response.json();
    return result.data.url;
  };

  const handleFileUpload = async (file: File) => {
    if (!editingDocument) return;
    
    setUploadingFile(true);
    try {
      const fileUrl = await uploadFile(file);
      setEditingDocument({ ...editingDocument, url: fileUrl });
    } catch (error) {
      console.error('Error uploading file:', error);
      alert('Ошибка загрузки файла');
    } finally {
      setUploadingFile(false);
    }
  };

  const handleExport = async (format: 'json' | 'csv' | 'xlsx') => {
    try {
      const response = await fetch('/api/export', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          type: 'documents',
          format: format
        }),
      });

      if (response.ok) {
        const result = await response.json();
        const downloadResponse = await fetch(`${result.data.downloadUrl}`);
        const blob = await downloadResponse.blob();
        const url = URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = result.data.filename;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        URL.revokeObjectURL(url);
      } else {
        throw new Error('Ошибка экспорта');
      }
    } catch (error) {
      console.error('Export error:', error);
      throw error;
    }
  };

  if (loading) {
    return <div className="p-8">Загрузка...</div>;
  }

  return (
    <div className="max-w-6xl mx-auto">
      <div className="mb-8">
        <h1 
          className="text-3xl font-bold mb-6"
          style={{
            fontFamily: 'Roboto Flex',
            fontWeight: 478,
            fontSize: '32px',
            lineHeight: '100%',
            letterSpacing: '-1px',
            fontVariationSettings: '"wdth" 10, "YTUC" 850, "YTAS" 900',
            color: '#F2F0F0'
          }}
        >
          Управление документами
        </h1>
        <div className="flex gap-4 flex-wrap">
          <button
            onClick={() => {
              setEditingDocument({
                id: 0,
                title: '',
                url: '',
                type: 'document',
                sort_order: documents.length + 1,
                is_active: true,
                created_at: '',
                updated_at: ''
              });
              setIsCreating(true);
            }}
            className="admin-button-primary"
          >
            Добавить документ
          </button>
          <button
            onClick={() => {
              setEditingDocument({
                id: 0,
                title: '',
                url: '',
                type: 'certificate',
                sort_order: documents.length + 1,
                is_active: true,
                created_at: '',
                updated_at: ''
              });
              setIsCreating(true);
            }}
            className="admin-button-secondary"
          >
            Добавить сертификат
          </button>
          <DataExporter onExport={handleExport} />
        </div>
      </div>

      {/* Список документов с drag and drop */}
      <DndContext
        sensors={sensors}
        collisionDetection={closestCenter}
        onDragEnd={handleDragEnd}
      >
        <SortableContext items={documents.map(document => document.id)} strategy={verticalListSortingStrategy}>
          <div className="grid gap-6">
            {documents.map((document, index) => (
              <SortableDocument
                key={document.id}
                document={document}
                index={index}
                onEdit={handleEdit}
                onDelete={handleDelete}
              />
            ))}
          </div>
        </SortableContext>
      </DndContext>

      {/* Форма редактирования */}
      {editingDocument && (
        <div className="fixed inset-0 bg-black bg-opacity-80 flex items-center justify-center z-50">
          <div className="admin-card max-w-2xl w-full mx-4 max-h-[90vh] overflow-y-auto">
            <h2 
              className="text-2xl font-bold mb-6"
              style={{
                fontFamily: 'Roboto Flex',
                fontWeight: 478,
                color: '#F2F0F0'
              }}
            >
              {isCreating ? 'Создание нового документа' : 'Редактирование документа'}
            </h2>
            
            <div className="space-y-4">
              <div>
                <label 
                  className="block text-sm font-medium mb-2"
                  style={{ color: '#B8B8B8' }}
                >
                  Название документа
                </label>
                <input
                  type="text"
                  value={editingDocument.title}
                  onChange={(e) => setEditingDocument({...editingDocument, title: e.target.value})}
                  className="admin-input"
                />
              </div>
              
              <div>
                <label 
                  className="block text-sm font-medium mb-2"
                  style={{ color: '#B8B8B8' }}
                >
                  Файл
                </label>
                <FileUpload
                  onFileSelect={handleFileUpload}
                  accept=".pdf,.doc,.docx,.xls,.xlsx"
                  maxSize={50}
                  disabled={uploadingFile}
                  showPreview={true}
                />
                {editingDocument.url && (
                  <div className="mt-2">
                    <input
                      type="text"
                      value={editingDocument.url}
                      onChange={(e) => setEditingDocument({...editingDocument, url: e.target.value})}
                      className="admin-input"
                      placeholder="Или введите URL вручную"
                    />
                  </div>
                )}
              </div>
              
              <div>
                <label 
                  className="block text-sm font-medium mb-2"
                  style={{ color: '#B8B8B8' }}
                >
                  Тип документа
                </label>
                <select
                  value={editingDocument.type}
                  onChange={(e) => setEditingDocument({...editingDocument, type: e.target.value as 'document' | 'certificate'})}
                  className="admin-input"
                >
                  <option value="document">Документ</option>
                  <option value="certificate">Сертификат</option>
                </select>
              </div>
              
              <div>
                <label 
                  className="block text-sm font-medium mb-2"
                  style={{ color: '#B8B8B8' }}
                >
                  Порядок сортировки
                </label>
                <input
                  type="number"
                  value={editingDocument.sort_order}
                  onChange={(e) => setEditingDocument({...editingDocument, sort_order: parseInt(e.target.value) || 0})}
                  className="admin-input"
                />
              </div>
              
              <div className="flex items-center">
                <input
                  type="checkbox"
                  id="is_active"
                  checked={editingDocument.is_active}
                  onChange={(e) => setEditingDocument({...editingDocument, is_active: e.target.checked})}
                  className="mr-2"
                  style={{ accentColor: '#D71920' }}
                />
                <label 
                  htmlFor="is_active" 
                  className="text-sm font-medium"
                  style={{ color: '#B8B8B8' }}
                >
                  Активен
                </label>
              </div>
            </div>
            
            <div className="flex gap-4 mt-8">
              <button
                onClick={handleSave}
                className="admin-button-success"
              >
                Сохранить
              </button>
              <button
                onClick={handleCancel}
                className="admin-button-secondary"
              >
                Отмена
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default DownloadsAdmin;
